import {Component} from '@angular/core';
import {UsersRoot} from './../Models/UsersRoot';
import {QuestionService} from './../Services/QuestionService';
import {CustomerRating} from './../Models/CustomerRating';
import {QuestionModel} from './../Models/QuestionModel';
import {Input, Output} from '@angular/core';
declare var jQuery:any;
@Component({
    selector: 'users',
    providers :[QuestionService],
    templateUrl: './../views/Users.html'
})
export class UsersComponent { 
    @Input() tooDate:string = null;
    @Input() frommDate:string = null;
    @Input() tooTime:string = null;
    @Input() frommTime:string = null;
    public gender:string = null;
    public usersroot:UsersRoot;
    public uniqueUserCount:number;
    public totalReviews:number;
    public returnRate:number;
    private questions: QuestionModel[] = [];
    @Output() public customerVisitsToBranches:CustomerRating[];
    public dateTime:Date;
    public questionUserId:string = null;
    public gradeUser:string = null;
    renderJQuery()
    {
        jQuery("#dpd3").datepicker({
            format: 'yyyy-mm-dd'
        });
                jQuery("#dpd4").datepicker({
            format: 'yyyy-mm-dd'
        });
         var time1 = jQuery('#timepicker3').timepicker({
               showMeridian : false 
        });
         var time2 = jQuery('#timepicker4').timepicker({
                showMeridian : false
        });
    }
    constructor(private _questionService : QuestionService) {
	    
        this._questionService.getQuestions()
                    .subscribe(
                        a=> {
                            this.questions = a;
                        });
            this._questionService.usersPageFiltering(null, null, null, null, null, null, null)
            .subscribe(
                a=> {
                    this.renderJQuery();
                    this.usersroot = a;
                    this.uniqueUserCount = this.usersroot.uniqueUserTotal;
                    this.totalReviews = this.usersroot.reviewsTotal;
                    this.returnRate = this.usersroot.returnRate;
                    this.customerVisitsToBranches = this.usersroot.customerRatings;
                    for(var i = 0;i < this.customerVisitsToBranches.length; i++)
                    {
                        this.dateTime = new Date(this.customerVisitsToBranches[i].ratingDate);
                        this.customerVisitsToBranches[i].rating = Math.round(this.customerVisitsToBranches[i].rating);
                        this.dateTime.setMonth(this.dateTime.getMonth()+1);
                        var tempDate = this.dateTime.getDate().toString() +"-"+ this.dateTime.getMonth().toString() +"-"+ this.dateTime.getFullYear().toString();
                        var time = this.dateTime.getHours().toString() +":"+ this.dateTime.getMinutes().toString();
                        this.customerVisitsToBranches[i].vistedDateString = tempDate;
                        this.customerVisitsToBranches[i].vistedTimeString = time;
                    }
                }
            );
        }
        
        changeRatingBtn(value)
        {
            var btn = document.getElementById('ratingBtn');
            var temp = null;
            if(value == 1)
            {
                temp = "A";
            }
            if(value == 2)
            {
                temp = "B";
            }
            if(value == 3)
            {
                temp = "C";
            }
            if(value == 4)
            {
                temp = "D";
            }
            if(value != "0")
            {
                this.gradeUser = temp;
                btn.textContent = temp;
                this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
            }
            else
            {
                this.gradeUser = null;
                btn.textContent = "All Ratings";
                this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
            }
            
        }
        changeUserQues(value)
        {
            var btn = document.getElementById('userQuesBtn');
            if(value != "0")
            {
                for(var i=0; i< this.questions.length;i++)
                {
                    if(value == this.questions[i]._id)
                    {
                        this.questionUserId = this.questions[i]._id;
                        btn.textContent = this.questions[i].questionHeading;
                    }
                }
            }
            else
            {
                this.questionUserId = null;
                btn.textContent = "All Questions";
            }
            this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
        }
        toDate()
        {
            
            if(this.tooDate == "")
            {
                this.tooDate = null;
            }
            if(this.tooTime == "")
            {
                this.tooTime = null;
            }
            if(this.frommDate == "")
            {
                this.frommDate = null;
            }
            if(this.frommTime == "")
            {
                this.frommTime = null;
            }
            this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
            
        }
        toTime()
        {
            if(this.tooDate == "")
            {
                this.tooDate = null;
            }
            if(this.tooTime == "")
            {
                this.tooTime = null;
            }
            if(this.frommDate == "")
            {
                this.frommDate = null;
            }
            if(this.frommTime == "")
            {
                this.frommTime = null;
            }
            this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
        }
        fromDate()
        {
            if(this.tooDate == "")
            {
                this.tooDate = null;
            }
            if(this.tooTime == "")
            {
                this.tooTime = null;
            }
            if(this.frommDate == "")
            {
                this.frommDate = null;
            }
            if(this.frommTime == "")
            {
                this.frommTime = null;
            }
            this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
        }
        fromTime()
        {
            if(this.tooDate == "")
            {
                this.tooDate = null;
            }
            if(this.tooTime == "")
            {
                this.tooTime = null;
            }
            if(this.frommDate == "")
            {
                this.frommDate = null;
            }
            if(this.frommTime == "")
            {
                this.frommTime = null;
            }
            this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
        }
        male()
        {
            if(this.gender == "male")
            {
                this.gender = null;
            }
            else
            {
                this.gender = "male";
            }
            
            this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
        }
        female()
        {
            if(this.gender == "female")
            {
                this.gender = null;
            }
            else
            {
                this.gender = "female";
            }
            this.sendCall(this.questionUserId, this.gradeUser, this.tooDate, this.tooTime, this.frommDate, this.frommTime, this.gender);
        }
        sendCall(questionId, graderUser, tooDate, tooTime, frommDate, frommTime, gender)
        {
                     
                    this._questionService.usersPageFiltering(questionId, graderUser, tooDate, tooTime, frommDate, frommTime, gender)
                    .subscribe(
                        a=> {
                            this.usersroot = a;
                            console.log(a);
                            this.uniqueUserCount = this.usersroot.uniqueUserTotal;
                            this.totalReviews = this.usersroot.reviewsTotal;
                            this.returnRate = this.usersroot.returnRate;
                            this.customerVisitsToBranches = this.usersroot.customerRatings;
                            for(var i = 0;i < this.customerVisitsToBranches.length; i++)
                            {
                                this.dateTime = new Date(this.customerVisitsToBranches[i].ratingDate);
                                this.customerVisitsToBranches[i].rating = Math.round(this.customerVisitsToBranches[i].rating);
                                this.dateTime.setMonth(this.dateTime.getMonth()+1);
                                var tempDate = this.dateTime.getDate().toString() +"-"+ this.dateTime.getMonth().toString() +"-"+ this.dateTime.getFullYear().toString();
                                var time = this.dateTime.getHours().toString() +":"+ this.dateTime.getMinutes().toString();
                                this.customerVisitsToBranches[i].vistedDateString = tempDate;
                                this.customerVisitsToBranches[i].vistedTimeString = time;
                            }
                        });
                
            
        }
}